<template>
  <div class="contact">
    <h1>聯絡我們</h1>
    <div class="container mb-5">
      <div class="row justify-content-center mb-3">
        <div class="col-10">
          <div class="card">
            <div class="card-header">
              社交平台
            </div>
            <div class="card-body">
              <div class="row justify-content-center">
                <a href="#" style="width: 50px;" class="mx-3">
                  <img class="w-100" src="@/assets/icon/facebook_logo.png" alt="">
                </a>
                <a href="#" style="width: 50px;" class="mx-3">
                  <img class="w-100" src="@/assets/icon/line_logo.png" alt="">
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row justify-content-center">
        <div class="col-10 col-lg-4 d-flex flex-column">
          <div class="card mb-3" v-if="user.id === ''">
            <div class="card-header">
              登入
            </div>
            <div class="card-body">
              <ValidationObserver v-slot='{ handleSubmit }'>
                <form @submit.prevent='handleSubmit(login)'>
                  <ValidationProvider name='Email' rules='required|email' v-slot="{ errors }">
                    <div class="form-group">
                      <label>Email</label>
                      <input type="email" class="form-control" v-model="loginForm.email">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Password' rules='required' v-slot="{ errors }">
                    <div class="form-group">
                      <label>密碼</label>
                      <input type="password" class="form-control" v-model="loginForm.password">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>
                  <div class="d-flex justify-content-end">
                    <input type="submit" value="登入" class="btn btn-primary">
                  </div>
                </form>
              </ValidationObserver>
            </div>
          </div>
          <div class="card mb-3" v-if="user.id === ''">
            <div class="card-header">
              使用 FB 登入
            </div>
            <div class="card-body d-flex justify-content-center align-items-center">
              <button class="btn btn-primary" @click.prevent="fbLogin">FB 登入</button>
            </div>
          </div>
          <div class="card mb-3" v-if="user.id === ''">
            <div class="card-header">
              快速註冊會員
            </div>
            <div class="card-body">
              <ValidationObserver v-slot='{ handleSubmit }'>
                <form @submit.prevent='handleSubmit(register)'>
                  <ValidationProvider name='Name' rules='required' v-slot="{ errors }">
                    <div class="form-group">
                      <label>姓名</label>
                      <input type="text" class="form-control" v-model="registerForm.name">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Gender' rules='required' v-slot="{ errors }">
                    <div class="form-group">
                      <label>性別</label>
                      <select class="form-control" v-model="registerForm.gender">
                        <option value="male">男</option>
                        <option value="female">女</option>
                      </select>
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Birthday' rules='required' v-slot="{ errors }">
                    <div class="form-group">
                      <label>生日</label>
                      <input type="date" class="form-control" v-model="registerForm.birthday">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Telephone' rules='required|numeric' v-slot="{ errors }">
                    <div class="form-group">
                      <label>電話</label>
                      <input type="text" class="form-control" v-model="registerForm.tel">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Email' rules='required|email' v-slot="{ errors }">
                    <div class="form-group">
                      <label>Email</label>
                      <input type="email" class="form-control" v-model="registerForm.email">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Password'
                  rules='required|min:8' v-slot="{ errors }">
                    <div class="form-group">
                      <label>密碼</label>
                      <input type="password" class="form-control" v-model="registerForm.password">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Confirm Password'
                  rules='required|confirmed:Password' v-slot="{ errors }">
                    <div class="form-group">
                      <label>確認密碼</label>
                      <input type="password" class="form-control" v-model="registerForm.confirm">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Register Captcha'
                  :rules='"required|is:" + registerForm.captchaCode' v-slot="{ errors }">
                    <div class="form-group">
                      <label>驗證碼</label>
                      <br>
                      <canvas class="canvas" height="40" width="75" id='registerCaptcha'></canvas>
                      <div class="d-flex align-items-center">
                        <input type="text" class="form-control flex-shrink-1"
                        v-model="registerForm.captchaInput">
                        <button class="flex-shrink-0 ml-2 btn btn-secondary"
                        @click.prevent="registerForm.captchaCode= createCaptcha('registerCaptcha')">
                        換一個</button>
                      </div>
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>
                  <hr>
                  <div class="d-flex justify-content-end">
                    <input type="submit" value="註冊會員" class="btn btn-primary">
                  </div>
                </form>
              </ValidationObserver>
            </div>
          </div>
          <div class="card mb-3" v-if="user.id !== ''">
            <div class="card-header">
              登出
            </div>
            <div class="card-body d-flex justify-content-center align-items-center">
              <button class="btn btn-secondary" @click.prevent="logOut">登出</button>
            </div>
          </div>
        </div>
        <div class="col-10 col-lg-6">
          <div class="card">
            <div class="card-header">
              <span v-if="user.id === ''">以訪客身分留言</span>
              <span v-if="user.id !== ''">會員留言</span>
            </div>
            <div class="card-body">
              <ValidationObserver v-slot='{ handleSubmit }'>
                <form @submit.prevent="handleSubmit(sendMail)">
                  <ValidationProvider name='Name' rules='required' v-slot='{ errors }'>
                    <div class="form-group">
                      <label>姓名</label>
                      <input type="text" class="form-control" v-model="newMail.name">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Telephone' rules='required|numeric'
                  v-slot='{ errors }'>
                    <div class="form-group">
                      <label>電話</label>
                      <input type="text" class="form-control" v-model="newMail.tel">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Email' rules='required|email' v-slot='{ errors }'>
                    <div class="form-group">
                      <label>Email</label>
                      <input type="email" class="form-control" v-model="newMail.email">
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Content' rules='required' v-slot='{ errors }'>
                    <div class="form-group">
                      <label>信件內容</label>
                      <ckeditor
                      :editor="ckEditorType"
                      v-model="newMail.content">
                      </ckeditor>
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>

                  <ValidationProvider name='Mail Captcha'
                  :rules='"required|is:" + newMail.captchaCode' v-slot="{ errors }">
                    <div class="form-group">
                      <label>驗證碼</label>
                      <br>
                      <canvas class="canvas" height="40" width="75" id='mailCaptcha'></canvas>
                      <div class="d-flex align-items-center">
                        <input type="text" class="form-control flex-shrink-1"
                        v-model="newMail.captchaInput">
                        <button class="flex-shrink-0 ml-2 btn btn-secondary"
                        @click.prevent="newMail.captchaCode = createCaptcha('mailCaptcha')">
                        換一個</button>
                      </div>
                      <span class="text-danger">{{ errors[0] }}</span>
                    </div>
                  </ValidationProvider>
                  <hr>
                  <div class="d-flex justify-content-end">
                    <input type="submit" value="寄出信件" class="btn btn-primary">
                  </div>
                </form>
              </ValidationObserver>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style>
  .ck-editor__editable {
    min-height: 200px;
    resize: vertical;
   }
</style>

<script>
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

export default {
  computed: {
    user() {
      let user = {};
      this.$bus.$on('son_get_data', (data) => {
        user = data.member;
      });
      this.$bus.$emit('son_call_data');
      return user;
    },
    members() {
      let members = [];
      this.$bus.$on('son_get_data', (data) => {
        members = data.members;
      });
      this.$bus.$emit('son_call_data');
      return members;
    },
  },
  watch: {
    'user.id': function () {
      this.createMail();
      this.createRegisterForm();
    },
  },
  data() {
    return {
      ckEditorType: ClassicEditor,
      loginForm: {
        email: '',
        password: '',
      },
      registerForm: {
      },
      newMail: {
      },
    };
  },
  methods: {
    createMail() {
      const code = this.createCaptcha('mailCaptcha');
      this.newMail = {
        id: this.user.id,
        time: Date.parse(new Date()),
        name: this.user.name,
        tel: this.user.tel,
        email: this.user.email,
        content: '',
        captchaCode: code,
        captchaInput: '',
        read: false,
        reply: {
          id: '',
          time: '',
          name: '',
          content: '',
        },
      };
    },
    login() {
      const thisMemberIndex = this.members.findIndex((item) => item.email === this.loginForm.email);
      if (thisMemberIndex < 0) {
        this.$bus.$emit('show_toast', 'warning', '無此用戶');
      } else {
        const thisMember = this.members[thisMemberIndex];
        if (thisMember.password !== this.loginForm.password) {
          this.loginForm.password = '';
          this.$bus.$emit('show_toast', 'warning', '密碼錯誤');
        } else {
          this.$bus.$emit('log_in', thisMember);
        }
      }
    },
    fbLogin() {
      this.$bus.$emit('fb_login', true);
    },
    createRegisterForm() {
      const code = this.createCaptcha('registerCaptcha');
      this.registerForm = {
        id: `member__${Date.parse(new Date())}`,
        name: '',
        gender: '',
        birthday: '',
        tel: '',
        email: '',
        password: '',
        confirm: '',
        favorites: [],
        captchaCode: code,
        captchaInput: '',
      };
    },
    register() {
      const cloneForm = JSON.parse(JSON.stringify(this.registerForm));
      delete cloneForm.confirm;
      delete cloneForm.captchaCode;
      delete cloneForm.captchaInput;
      this.$bus.$emit('add_new_member', cloneForm);
      this.createRegisterForm();
    },
    logOut() {
      this.$bus.$emit('log_out');
    },
    sendMail() {
      const cloneMail = JSON.parse(JSON.stringify(this.newMail));
      delete cloneMail.captchaCode;
      delete cloneMail.captchaInput;
      if (this.newMail.id === '') {
        this.newMail.id = 'strangerMail';
        this.$bus.$emit('add_new_mail', cloneMail);
      } else {
        this.$bus.$emit('add_new_mail', cloneMail);
      }
      this.createMail();
    },
    createCaptcha(id) {
      let code = '';
      const createCanvas = () => {
        this.input_code = '';
        const codelength = 4;
        const codebase = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789';
        for (let index = 0; index < codelength; index += 1) {
          const codebaseIndex = parseInt(Math.floor(Math.random() * codebase.length), 10);
          const codeAlphabet = codebase[codebaseIndex];
          code += codeAlphabet;
        }
        this.registerForm.captchaCode = code;
        const canvas = document.getElementById(id);
        // 宣告為2D繪圖
        const canvas2d = canvas.getContext('2d');
        // 繪製背景色
        canvas2d.fillStyle = 'white';
        canvas2d.fillRect(0, 0, 100, 100);
        // 繪製驗證碼
        canvas2d.font = 'bolder 25px Roboto';
        canvas2d.fillStyle = 'black';
        canvas2d.fillText(code, 5, 30);
        // 繪製干擾線
        canvas2d.strokeStyle = 'black';
        canvas2d.beginPath();
        canvas2d.lineWidth = '1';
        canvas2d.moveTo(Math.floor(Math.random() * 70) + 5, 0);
        canvas2d.lineTo(Math.floor(Math.random() * 60) + 5, 25);
        canvas2d.moveTo(Math.floor(Math.random() * 70) + 5, 10);
        canvas2d.lineTo(Math.floor(Math.random() * 70) + 5, 28);
        canvas2d.stroke();
        // 繪製干擾點
        canvas2d.strokeStyle = 'black';
        canvas2d.beginPath();
        canvas2d.lineWidth = '1';
        for (let i = 0; i < 100; i += 1) {
          const bex = Math.floor(Math.random() * 78);
          const bey = Math.floor(Math.random() * 28);
          const tex = parseFloat((Math.random() * 2).toFixed(2));
          const tey = parseFloat((Math.random() * 2).toFixed(2));
          const enx = bex + tex;
          const eny = bey + tey;
          canvas2d.moveTo(bex, bey);
          canvas2d.lineTo(enx, eny);
        }
        canvas2d.stroke();
      };
      createCanvas();
      return code;
    },
  },
  mounted() {
    this.createMail();
    this.createRegisterForm();
  },
};
</script>
